import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { toast } from 'sonner';
import { Mail, Lock, User, Loader2, Eye, EyeOff } from 'lucide-react';
import { supabase } from '@/integrations/supabase/client';

const RegisterForm = () => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [isSigningIn, setIsSigningIn] = useState(false);
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    password: '',
    confirmPassword: '',
    studentNumber: '',
  });
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});

  useEffect(() => {
    const checkAuth = async () => {
      const { data: { session } } = await supabase.auth.getSession();
      if (session) {
        navigate('/login');
      }
    };
    checkAuth();
  }, [navigate]);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    // Validate full name
    if (!formData.fullName.trim()) {
      newErrors.fullName = 'Full name is required';
    }

    // Validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      newErrors.email = 'Please enter a valid email address';
    }

    // Validate student number (allowing both text and digits)
    const studentNumberRegex = /^[A-Za-z0-9]+$/;
    if (!studentNumberRegex.test(formData.studentNumber)) {
      newErrors.studentNumber = 'Student number must contain only letters and numbers';
    }

    // Validate password
    if (formData.password.length < 6) {
      newErrors.password = 'Password must be at least 6 characters long';
    }

    // Validate password match
    if (formData.password !== formData.confirmPassword) {
      newErrors.confirmPassword = 'Passwords do not match';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      // Show loading toast
      toast("Creating your account...", {
        description: "Please wait while we set up your account.",
      });

      // Try signing up first
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: formData.email,
        password: formData.password,
        options: {
          data: {
            full_name: formData.fullName,
            student_number: formData.studentNumber,
            role: 'student'
          },
          emailRedirectTo: `${window.location.origin}/auth/callback`
        }
      });

      if (authError) {
        console.error('Signup error:', authError);
        if (authError.message === 'User already registered') {
          setIsSigningIn(true);
          toast("Account exists", {
            description: "Attempting to sign you in...",
          });

          try {
            const { data: signInData, error: signInError } = await supabase.auth.signInWithPassword({
              email: formData.email,
              password: formData.password,
            });
            
            if (signInError) {
              throw new Error(signInError.message);
            }

            if (!signInData.user) {
              throw new Error('No user data received after successful sign in');
            }

            // Get user profile after successful sign in
            const { data: profileData, error: profileError } = await supabase
              .from('user_profiles')
              .select('*')
              .eq('id', signInData.user.id)
              .single();

            if (profileError) {
              console.error('Profile fetch error:', profileError);
              throw new Error('Failed to fetch user profile');
            }

            if (!profileData) {
              throw new Error('User profile not found');
            }

            // Update last activity
            await supabase
              .from('user_profiles')
              .update({ last_activity_at: new Date().toISOString() })
              .eq('id', signInData.user.id);
            
            toast("Welcome back!", {
              description: "Successfully signed in to your account.",
            });

            // Store user data in localStorage
            localStorage.setItem('user', JSON.stringify({
              id: signInData.user.id,
              email: signInData.user.email,
              role: profileData.role,
              permissions: profileData.permissions
            }));

            // Set session in Supabase
            await supabase.auth.setSession({
              access_token: signInData.session?.access_token || '',
              refresh_token: signInData.session?.refresh_token || ''
            });
            
            // Redirect to dashboard
            window.location.href = '/dashboard';
            return;
          } catch (error) {
            throw new Error('Failed to sign in with existing account');
          } finally {
            setIsSigningIn(false);
          }
        }
        throw authError;
      }

      if (authData.user) {
        // Show profile creation toast
        toast("Creating your profile...", {
          description: "Setting up your user profile.",
        });

        // Create user profile
        const { error: profileError } = await supabase
          .from('user_profiles')
          .insert({
            id: authData.user.id,
            email: formData.email,
            full_name: formData.fullName,
            student_number: formData.studentNumber,
            role: 'student',
            permissions: {
              canBook: true,
              canViewOwnBookings: true,
              canManageBookings: false,
              canManageUsers: false,
              canManageResources: false,
              canVerifyBookings: false
            }
          })
          .select();

        if (profileError) {
          console.error('Profile creation error:', profileError);
          throw new Error('Failed to create user profile. Please try again.');
        }

        // Show success toast
        toast("Account Created Successfully! 🎉", {
          description: (
            <div className="mt-2">
              <p>Your account has been created successfully.</p>
              <p className="mt-1">Please check your email ({formData.email}) for verification.</p>
              <p className="mt-1">You can now log in to your account.</p>
            </div>
          ),
        });

        // Redirect to login page after a short delay
        setTimeout(() => {
          navigate('/login');
        }, 3000);
      } else {
        throw new Error('Failed to create user account');
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "Failed to create account";
      toast("Error", {
        description: errorMessage,
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="space-y-2">
        <Label htmlFor="fullName">Full Name</Label>
        <div className="relative">
          <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            id="fullName"
            name="fullName"
            type="text"
            placeholder="John Doe"
            className={`pl-10 ${errors.fullName ? 'border-red-500' : ''}`}
            value={formData.fullName}
            onChange={handleChange}
            required
          />
        </div>
        {errors.fullName && <p className="text-sm text-red-500 mt-1">{errors.fullName}</p>}
      </div>

      <div className="space-y-2">
        <Label htmlFor="email">Email</Label>
        <div className="relative">
          <Mail className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            id="email"
            name="email"
            type="email"
            placeholder="university@email.com"
            className={`pl-10 ${errors.email ? 'border-red-500' : ''}`}
            value={formData.email}
            onChange={handleChange}
            required
          />
        </div>
        {errors.email && <p className="text-sm text-red-500 mt-1">{errors.email}</p>}
      </div>

      <div className="space-y-2">
        <Label htmlFor="studentNumber">Student Number</Label>
        <div className="relative">
          <User className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            id="studentNumber"
            name="studentNumber"
            type="text"
            placeholder="2022XXXXX"
            className={`pl-10 ${errors.studentNumber ? 'border-red-500' : ''}`}
            value={formData.studentNumber}
            onChange={handleChange}
            required
          />
        </div>
        {errors.studentNumber && <p className="text-sm text-red-500 mt-1">{errors.studentNumber}</p>}
      </div>
      
      <div className="space-y-2">
        <Label htmlFor="password">Password</Label>
        <div className="relative">
          <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            id="password"
            name="password"
            type={showPassword ? "text" : "password"}
            className={`pl-10 ${errors.password ? 'border-red-500' : ''}`}
            value={formData.password}
            onChange={handleChange}
            required
            minLength={8}
          />
          <Button
            type="button"
            variant="ghost"
            size="sm"
            className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
            onClick={() => setShowPassword(!showPassword)}
          >
            {showPassword ? (
              <EyeOff className="h-4 w-4 text-gray-500" />
            ) : (
              <Eye className="h-4 w-4 text-gray-500" />
            )}
          </Button>
        </div>
        {errors.password && <p className="text-sm text-red-500 mt-1">{errors.password}</p>}
      </div>

      <div className="space-y-2">
        <Label htmlFor="confirmPassword">Confirm Password</Label>
        <div className="relative">
          <Lock className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
          <Input
            id="confirmPassword"
            name="confirmPassword"
            type={showPassword ? "text" : "password"}
            className={`pl-10 ${errors.confirmPassword ? 'border-red-500' : ''}`}
            value={formData.confirmPassword}
            onChange={handleChange}
            required
            minLength={8}
          />
        </div>
        {errors.confirmPassword && <p className="text-sm text-red-500 mt-1">{errors.confirmPassword}</p>}
      </div>
      
      <Button
        type="submit"
        className="w-full bg-university-blue hover:bg-university-blue/90"
        disabled={isLoading || isSigningIn}
      >
        {isLoading ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            Creating Account...
          </>
        ) : isSigningIn ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            Signing in...
          </>
        ) : 'Create Account'}
      </Button>
    </form>
  );
};

export default RegisterForm; 