import React, { useState } from 'react';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent } from '@/components/ui/card';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Button } from '@/components/ui/button';
import { format } from 'date-fns';
import BookingDetailsForm from './BookingDetailsForm';
import { rooms } from '@/data/rooms';

const BookingCalendar = () => {
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [selectedRoom, setSelectedRoom] = useState<string>("");
  const [selectedTimeSlot, setSelectedTimeSlot] = useState<string>("");
  const [showDetailsForm, setShowDetailsForm] = useState(false);

  // Generate time slots
  const generateTimeSlots = () => {
    const slots = [];
    for (let hour = 8; hour < 20; hour++) {
      slots.push(`${hour}:00 - ${hour + 1}:00`);
    }
    return slots;
  };

  const timeSlots = generateTimeSlots();

  const handleNext = () => {
    setShowDetailsForm(true);
  };

  const handleBack = () => {
    setShowDetailsForm(false);
  };

  if (showDetailsForm && date && selectedRoom && selectedTimeSlot) {
    const selectedRoomData = rooms.find(r => r.id === selectedRoom);
    return (
      <BookingDetailsForm
        bookingData={{
          date,
          room: selectedRoomData?.name || '',
          timeSlot: selectedTimeSlot,
          roomId: selectedRoom
        }}
        onBack={handleBack}
      />
    );
  }

  return (
    <Card>
      <CardContent className="p-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <div>
            <h3 className="text-lg font-medium mb-4">Select Date</h3>
            <Calendar
              mode="single"
              selected={date}
              onSelect={setDate}
              className="rounded-md border"
              disabled={(date) => date < new Date()}
            />
          </div>

          <div className="space-y-6">
            <div>
              <h3 className="text-lg font-medium mb-4">Select Room</h3>
              <Select value={selectedRoom} onValueChange={setSelectedRoom}>
                <SelectTrigger>
                  <SelectValue placeholder="Select a room" />
                </SelectTrigger>
                <SelectContent>
                  {rooms.map((room) => (
                    <SelectItem key={room.id} value={room.id}>
                      {room.name} (Capacity: {room.capacity})
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            {selectedRoom && (
              <div>
                <h3 className="text-lg font-medium mb-4">Select Time Slot</h3>
                <div className="grid grid-cols-2 gap-2">
                  {timeSlots.map((slot) => (
                    <Button
                      key={slot}
                      variant={selectedTimeSlot === slot ? "default" : "outline"}
                      className="justify-start"
                      onClick={() => setSelectedTimeSlot(slot)}
                    >
                      {slot}
                    </Button>
                  ))}
                </div>
              </div>
            )}
            
            {selectedRoom && selectedTimeSlot && date && (
              <div className="mt-6 pt-6 border-t">
                <h3 className="text-lg font-medium mb-2">Booking Summary</h3>
                <p className="text-sm text-gray-600 mb-1">
                  <strong>Date:</strong> {format(date, 'PPP')}
                </p>
                <p className="text-sm text-gray-600 mb-1">
                  <strong>Room:</strong> {rooms.find(r => r.id === selectedRoom)?.name}
                </p>
                <p className="text-sm text-gray-600 mb-4">
                  <strong>Time:</strong> {selectedTimeSlot}
                </p>
                <Button 
                  className="w-full bg-university-blue hover:bg-university-blue/90"
                  onClick={handleNext}
                >
                  Proceed to Details
                </Button>
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export default BookingCalendar;
