import { useEffect, useState } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { User } from '@supabase/supabase-js';
import { ExtendedUser, UserRole, UserPermissions } from '@/types/auth';

export function useAuth() {
  const [user, setUser] = useState<ExtendedUser | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  useEffect(() => {
    const getSession = async () => {
      try {
        const { data: { session }, error: sessionError } = await supabase.auth.getSession();
        if (sessionError) throw sessionError;
        setUser(session?.user as ExtendedUser ?? null);
      } catch (err) {
        setError(err instanceof Error ? err : new Error('Failed to get session'));
      } finally {
        setLoading(false);
      }
    };

    getSession();

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user as ExtendedUser ?? null);
      setLoading(false);
    });

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  const hasRole = (requiredRole: UserRole): boolean => {
    if (!user || !user.role) return false;
    return user.role === requiredRole;
  };

  const hasPermission = (requiredPermission: keyof UserPermissions): boolean => {
    if (!user || !user.permissions) return false;
    return Boolean(user.permissions[requiredPermission]);
  };

  const signInWithEmail = async (email: string, password: string) => {
    try {
      const { data, error } = await supabase.auth.signInWithPassword({ email, password });
      if (error) throw error;
      if (!data?.session) throw new Error('No session returned');
      
      // Get user profile
      const { data: profile, error: profileError } = await supabase
        .from('user_profiles')
        .select('role, permissions')
        .eq('id', data.user.id)
        .single();

      if (profileError) throw profileError;

      const userData = {
        id: data.user.id,
        email: data.user.email!,
        role: profile?.role || 'user',
        permissions: profile?.permissions || {
          canBook: true,
          canManageBookings: false,
          canManageUsers: false,
          canManageResources: false,
          canViewBookings: true,
          canVerifyBookings: false
        },
      };

      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));

      return data;
    } catch (err) {
      throw err instanceof Error ? err : new Error('Failed to sign in');
    }
  };

  const signOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      setUser(null);
      localStorage.removeItem('user');
    } catch (err) {
      throw err instanceof Error ? err : new Error('Failed to sign out');
    }
  };

  return { 
    user, 
    loading, 
    error,
    hasRole,
    hasPermission,
    signInWithEmail,
    signOut
  };
} 