import { Toaster as Sonner } from "@/components/ui/sonner";
import { TooltipProvider } from "@/components/ui/tooltip";
import { Routes, Route, useParams, Navigate } from "react-router-dom";
import ErrorBoundary from "@/components/ui/ErrorBoundary";
import ProtectedRoute from "@/components/auth/ProtectedRoute";
import Index from "@/pages/Index";
import Login from "@/pages/Login";
import Register from "@/pages/Register";
import Bookings from "@/pages/Bookings";
import Map from "@/pages/Map";
import Blog from "@/pages/Blog";
import BlogDetail from "@/pages/BlogDetail";
import BlogForm from "@/pages/BlogForm";
import About from "@/pages/About";
import NotFound from "@/pages/NotFound";
import Admin from "@/pages/Admin";
import Dashboard from "@/pages/Dashboard";
import InnovationHubApplicationPage from '@/pages/InnovationHubApplication';
import PasswordResetForm from '@/components/auth/components/PasswordResetForm';
import Unauthorized from '@/pages/Unauthorized';
import AuthCallback from '@/components/auth/components/AuthCallback';

const ResetPasswordWrapper = () => {
  const { token } = useParams();
  return <PasswordResetForm mode="reset" token={token} />;
};

const App = () => (
  <ErrorBoundary>
    <TooltipProvider>
      <div className="min-h-screen">
        <Sonner />
        <Routes>
          {/* Auth Routes */}
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/unauthorized" element={<Unauthorized />} />
          <Route path="/reset-password" element={<PasswordResetForm mode="request" />} />
          <Route path="/reset-password/:token" element={<ResetPasswordWrapper />} />
          <Route path="/auth/callback" element={<AuthCallback />} />
          
          {/* Public Routes */}
          <Route path="/" element={<Index />} />
          <Route path="/home" element={<Navigate to="/" replace />} />
          <Route path="/bookings" element={<Bookings />} />
          <Route path="/map" element={<Map />} />
          <Route path="/blog" element={<Blog />} />
          <Route path="/blog/:id" element={<BlogDetail />} />
          <Route path="/about" element={<About />} />
          
          {/* Protected Routes */}
          <Route path="/blog/create" element={
            <ProtectedRoute requiredRole="admin">
              <BlogForm />
            </ProtectedRoute>
          } />
          
          <Route path="/blog/edit/:id" element={
            <ProtectedRoute requiredRole="admin">
              <BlogForm />
            </ProtectedRoute>
          } />
          
          <Route path="/admin" element={
            <ProtectedRoute requiredRole="admin">
              <Admin />
            </ProtectedRoute>
          } />
          
          <Route path="/dashboard" element={
            <ProtectedRoute>
              <Dashboard />
            </ProtectedRoute>
          } />
          
          <Route path="/ru/apply" element={
            <ProtectedRoute>
              <InnovationHubApplicationPage />
            </ProtectedRoute>
          } />
          
          {/* Catch all route */}
          <Route path="*" element={<NotFound />} />
        </Routes>
      </div>
    </TooltipProvider>
  </ErrorBoundary>
);

export default App;
