import React from 'react'
import ReactDOM from 'react-dom/client'
import { HashRouter } from 'react-router-dom'
import { QueryClient, QueryClientProvider } from '@tanstack/react-query'
import { TooltipProvider } from '@radix-ui/react-tooltip'
import { AuthProvider } from './contexts/AuthContext'
import ErrorBoundary from './components/ui/ErrorBoundary'
import App from './App'
import './index.css'
import { LoadingSpinner, ErrorDisplay } from './components/ui/LoadingComponents'

// Create a container for the root element
const container = document.getElementById('root')
if (!container) throw new Error('Failed to find the root element')

// Initialize the app
try {
  const queryClient = new QueryClient({
    defaultOptions: {
      queries: {
        retry: 1,
        refetchOnWindowFocus: false,
      },
    },
  })
  const root = ReactDOM.createRoot(container)
  root.render(
    <React.StrictMode>
      <ErrorBoundary>
        <QueryClientProvider client={queryClient}>
          <TooltipProvider>
            <AuthProvider>
              <HashRouter>
                <App />
              </HashRouter>
            </AuthProvider>
          </TooltipProvider>
        </QueryClientProvider>
      </ErrorBoundary>
    </React.StrictMode>
  )
  console.log("Application successfully initialized")
} catch (error: unknown) {
  console.error("Error initializing application:", error)
  
  // Handle different types of errors
  const errorDetails = error instanceof Error 
    ? error.stack || error.message 
    : typeof error === 'string' 
      ? error 
      : 'An unknown error occurred'

  const root = ReactDOM.createRoot(container)
  root.render(
    <ErrorDisplay 
      title="Application Error"
      message="Sorry, an error occurred while initializing the application."
      details={errorDetails}
    />
  )
}

// Add the spinning animation to the global styles
const style = document.createElement('style')
style.textContent = `
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
`
document.head.appendChild(style)
