import React, { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import Layout from '@/components/layout/Layout';
import PageHeader from '@/components/ui/PageHeader';
import BlogCard, { BlogPost } from '@/components/blog/BlogCard';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Search, Plus, Clock } from 'lucide-react';
import { Link } from 'react-router-dom';
import { supabase } from '@/integrations/supabase/client';
import { toast } from 'sonner';
import { BlogDiagnostic } from '@/components/BlogDiagnostic';
import { blogPosts } from '@/data/blogPosts';
import { Card, CardContent } from '@/components/ui/card';

// Define the database blog post type with proper author type
export interface SupabaseBlogPost {
  id: string;
  title: string;
  excerpt: string;
  content: string;
  image_url: string;
  created_at: string;
  updated_at: string;
  published: boolean;
  author_id: string;
  author?: {
    id: string;
    name: string;
    avatar_initials: string;
  }[];
}

// Function to fetch blog posts from Supabase
const fetchBlogPosts = async (): Promise<BlogPost[]> => {
  console.log('Starting fetchBlogPosts...');
  console.log('Supabase client initialized:', !!supabase);
  
  try {
    // Check authentication status
    const { data: { session }, error: authError } = await supabase.auth.getSession();
    console.log('Auth status:', { hasSession: !!session, authError });

    // If no session, try to sign in anonymously
    if (!session) {
      console.log('No session found, attempting anonymous sign in...');
      const { error: signInError } = await supabase.auth.signInWithPassword({
        email: 'anonymous@example.com',
        password: 'anonymous'
      });
      if (signInError) {
        console.log('Anonymous sign in failed:', signInError);
      }
    }

    console.log('Attempting to fetch blog posts from Supabase...');
    const { data, error } = await supabase
      .from('blog_posts')
      .select(`
        id,
        title,
        excerpt,
        content,
        image_url,
        created_at,
        updated_at,
        published,
        author_id,
        author:blog_authors(id, name, avatar_initials)
      `)
      .eq('published', true)
      .order('created_at', { ascending: false });

    console.log('Supabase response:', { 
      data: data?.length || 0, 
      error: error ? {
        code: error.code,
        message: error.message,
        details: error.details,
        hint: error.hint
      } : null 
    });

    if (error) {
      console.error('Error fetching blog posts:', error);
      console.error('Error details:', {
        code: error.code,
        message: error.message,
        details: error.details,
        hint: error.hint
      });
      
      // If it's an auth error, try to refresh the session
      if (error.code === '401' || error.code === '403') {
        const { error: refreshError } = await supabase.auth.refreshSession();
        if (refreshError) {
          console.error('Failed to refresh session:', refreshError);
        }
      }
      
      // If it's a CORS error, try to fetch with different options
      if (error.message?.includes('CORS')) {
        console.log('CORS error detected, trying alternative fetch method...');
        const response = await fetch(`${supabaseUrl}/rest/v1/blog_posts?select=*`, {
          headers: {
            'apikey': supabaseAnonKey,
            'Authorization': `Bearer ${supabaseAnonKey}`
          }
        });
        if (response.ok) {
          const data = await response.json();
          console.log('Alternative fetch successful:', data);
          return transformPosts(data);
        }
      }
      
      throw error;
    }

    if (!data) {
      console.log('No blog posts found in response');
      return [];
    }

    console.log('Blog posts fetched successfully:', data);
    return transformPosts(data);
  } catch (error) {
    console.error('Error in fetchBlogPosts:', error);
    return [];
  }
};

// Helper function to transform posts
const transformPosts = (data: any[]): BlogPost[] => {
  return data.map(post => ({
    id: post.id,
    title: post.title,
    excerpt: post.excerpt,
    author: {
      name: post.author?.[0]?.name || 'Unknown Author',
      avatarInitials: post.author?.[0]?.avatar_initials || 'UA',
    },
    date: new Date(post.created_at).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }),
    readTime: '5 min read',
    imageUrl: post.image_url || 'https://images.unsplash.com/photo-1531482615713-2afd69097998?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80',
  }));
};

const Blog = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [showDiagnostics, setShowDiagnostics] = useState(false);

  // Fetch blog posts using React Query
  const { data: supabasePosts, isLoading, error } = useQuery({
    queryKey: ['blogPosts'],
    queryFn: fetchBlogPosts,
    retry: 1,
    staleTime: 1000 * 60 * 5, // 5 minutes
  });

  console.log('Blog component state:', { supabasePosts, isLoading, error });

  // If Supabase fetch fails or returns no data, use the static blog posts data
  const posts = supabasePosts?.length ? supabasePosts : blogPosts;
  
  // Show error toast if fetch fails
  React.useEffect(() => {
    if (error) {
      console.error('Blog posts fetch error:', error);
      toast.error('Failed to fetch blog posts', {
        description: 'Using fallback data instead.'
      });
    }
  }, [error]);
  
  const filteredPosts = searchTerm
    ? posts.filter(post => 
        post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        post.excerpt.toLowerCase().includes(searchTerm.toLowerCase())
      )
    : posts;

  // Get recent updates (last 3 posts)
  const recentUpdates = posts.slice(0, 3);

  return (
    <Layout>
      <PageHeader 
        title="Blog & Updates" 
        subtitle="Latest news and information from the Software Development Centre"
      />
      <div className="container mx-auto px-4 py-12">
        {/* Recent Updates Section */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold mb-6 flex items-center">
            <Clock className="mr-2 h-6 w-6" />
            Recent Updates
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {recentUpdates.map((post) => (
              <Card key={post.id} className="hover:shadow-lg transition-shadow">
                <CardContent className="p-4">
                  <div className="aspect-video mb-4 rounded-md overflow-hidden">
                    <img
                      src={post.imageUrl}
                      alt={post.title}
                      className="w-full h-full object-cover"
                    />
                  </div>
                  <h3 className="font-semibold text-lg mb-2 line-clamp-2">{post.title}</h3>
                  <p className="text-gray-600 text-sm mb-4 line-clamp-2">{post.excerpt}</p>
                  <div className="flex items-center justify-between text-sm text-gray-500">
                    <span>{post.date}</span>
                    <span>{post.readTime}</span>
                  </div>
                  <Link to={`/blog/${post.id}`}>
                    <Button variant="link" className="p-0 mt-2">
                      Read more →
                    </Button>
                  </Link>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>

        {/* Search and Create Section */}
        <div className="flex flex-col md:flex-row justify-between items-center mb-12 gap-4">
          <div className="w-full md:w-96 relative">
            <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <Input
              type="search"
              placeholder="Search blog posts..."
              className="pl-10"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          <div className="flex gap-2">
            <Button 
              variant="outline" 
              onClick={() => setShowDiagnostics(!showDiagnostics)}
            >
              {showDiagnostics ? 'Hide Diagnostics' : 'Show Diagnostics'}
            </Button>
            <Link to="/blog/create">
              <Button className="w-full md:w-auto">
                <Plus className="h-4 w-4 mr-2" />
                Create Post
              </Button>
            </Link>
          </div>
        </div>

        {showDiagnostics && <BlogDiagnostic />}
        
        {/* All Posts Section */}
        <div>
          <h2 className="text-2xl font-bold mb-6">All Posts</h2>
          {isLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {[1, 2, 3].map((i) => (
                <div key={i} className="animate-pulse">
                  <div className="h-48 bg-gray-200 rounded mb-4"></div>
                  <div className="h-6 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-4 bg-gray-200 rounded w-1/2 mb-4"></div>
                </div>
              ))}
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {filteredPosts.map((post) => (
                <BlogCard key={post.id} post={post} />
              ))}
            </div>
          )}
        </div>
      </div>
    </Layout>
  );
};

export default Blog;
