import { useState } from 'react';
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

export function InnovationHubForm() {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button 
          className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-2 px-4 rounded-lg transition-colors"
          onClick={() => setIsOpen(true)}
        >
          Apply for Innovation Hub
        </Button>
      </DialogTrigger>
      <DialogContent className="max-w-4xl h-[80vh]">
        <DialogHeader>
          <DialogTitle>Innovation Hub Application Form</DialogTitle>
        </DialogHeader>
        <div className="w-full h-full">
          <iframe
            src="https://docs.google.com/forms/d/e/1FAIpQLSevU-7nhDxmmIRvmW_MCAc9V-xPMjWfadFlqGJQdiQWQNEFBA/viewform?embedded=true"
            className="w-full h-full border-0"
            title="Innovation Hub Application Form"
          />
        </div>
      </DialogContent>
    </Dialog>
  );
} 