import React, { useEffect, useState } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { Card, CardContent } from '@/components/ui/card';

export const BlogDiagnostic: React.FC = () => {
  const [testResults, setTestResults] = useState<{
    envVars: boolean;
    auth: boolean;
    blogPosts: boolean;
    blogAuthors: boolean;
    error: string | null;
  }>({
    envVars: false,
    auth: false,
    blogPosts: false,
    blogAuthors: false,
    error: null
  });

  useEffect(() => {
    const runDiagnostics = async () => {
      try {
        // Test 1: Check environment variables
        const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
        const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;
        
        if (!supabaseUrl || !supabaseAnonKey) {
          throw new Error('Missing Supabase environment variables');
        }
        setTestResults(prev => ({ ...prev, envVars: true }));

        // Test 2: Check authentication
        const { data: authData, error: authError } = await supabase.auth.getSession();
        if (authError) throw authError;
        setTestResults(prev => ({ ...prev, auth: true }));

        // Test 3: Check blog_posts table
        const { data: postsData, error: postsError } = await supabase
          .from('blog_posts')
          .select('*')
          .limit(1);
        
        if (postsError) throw postsError;
        setTestResults(prev => ({ ...prev, blogPosts: true }));

        // Test 4: Check blog_authors table
        const { data: authorsData, error: authorsError } = await supabase
          .from('blog_authors')
          .select('*')
          .limit(1);
        
        if (authorsError) throw authorsError;
        setTestResults(prev => ({ ...prev, blogAuthors: true }));

      } catch (error) {
        console.error('Diagnostic Error:', error);
        setTestResults(prev => ({ 
          ...prev, 
          error: error instanceof Error ? error.message : 'Unknown error occurred'
        }));
      }
    };

    runDiagnostics();
  }, []);

  return (
    <Card className="max-w-2xl mx-auto mt-8">
      <CardContent className="p-6">
        <h2 className="text-xl font-semibold mb-4">Blog System Diagnostic</h2>
        
        <div className="space-y-4">
          <div className={`p-3 rounded ${testResults.envVars ? 'bg-green-50' : 'bg-gray-50'}`}>
            <span className="font-medium">Environment Variables:</span>{' '}
            {testResults.envVars ? '✅ Configured' : '❌ Missing'}
          </div>

          <div className={`p-3 rounded ${testResults.auth ? 'bg-green-50' : 'bg-gray-50'}`}>
            <span className="font-medium">Authentication:</span>{' '}
            {testResults.auth ? '✅ Connected' : '❌ Failed'}
          </div>

          <div className={`p-3 rounded ${testResults.blogPosts ? 'bg-green-50' : 'bg-gray-50'}`}>
            <span className="font-medium">Blog Posts Table:</span>{' '}
            {testResults.blogPosts ? '✅ Accessible' : '❌ Inaccessible'}
          </div>

          <div className={`p-3 rounded ${testResults.blogAuthors ? 'bg-green-50' : 'bg-gray-50'}`}>
            <span className="font-medium">Blog Authors Table:</span>{' '}
            {testResults.blogAuthors ? '✅ Accessible' : '❌ Inaccessible'}
          </div>

          {testResults.error && (
            <div className="p-3 rounded bg-red-50 text-red-600">
              <span className="font-medium">Error:</span>{' '}
              {testResults.error}
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}; 