import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/hooks/useAuth';
import { AuthError } from '@supabase/supabase-js';

// Import components
import EmailLoginForm from '@/components/auth/components/EmailLoginForm';
import CardScannerForm from '@/components/auth/components/CardScannerForm';
import DevModeNotice from '@/components/auth/components/DevModeNotice';
import AutoLoginLoader from '@/components/auth/components/AutoLoginLoader';

// Import configuration
import { DEV_MODE, DEV_AUTO_LOGIN, DEV_USERS } from '@/components/auth/config/constants';

const LoginForm = () => {
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();
  const { signInWithEmail, loading } = useAuth();
  const navigate = useNavigate();
  
  const handleLogin = async (email: string, password: string) => {
    setError(null);

    try {
      const { data } = await signInWithEmail(email, password);
      if (data?.session) {
        navigate('/dashboard');
      } else {
        throw new Error('No session created after sign in');
      }
    } catch (err) {
      const authError = err as AuthError;
      setError(authError.message || 'Login failed. Please try again.');
      toast.error("Login Failed", {
        description: authError.message || 'Please check your credentials and try again.'
      });
    }
  };
  
  return (
    <div className="w-full max-w-md mx-auto">
      {DEV_MODE && <DevModeNotice users={DEV_USERS} />}
      
      <Tabs defaultValue="email" className="w-full">
        <TabsList className="grid grid-cols-2 mb-6">
          <TabsTrigger value="email">Email Login</TabsTrigger>
          <TabsTrigger value="card">Card Scanner</TabsTrigger>
        </TabsList>
        
        <TabsContent value="email">
          <EmailLoginForm onLogin={handleLogin} />
        </TabsContent>
        
        <TabsContent value="card">
          <CardScannerForm />
        </TabsContent>
      </Tabs>
    </div>
  );
};

export default LoginForm;
