import React, { useState } from 'react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import Layout from '@/components/layout/Layout';
import PageHeader from '@/components/ui/PageHeader';
import BookingCalendar from '@/components/booking/BookingCalendar';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { supabase } from '@/integrations/supabase/client';
import { Card, CardContent } from '@/components/ui/card';
import { format } from 'date-fns';
import { Clock, CheckCircle2, AlertCircle } from 'lucide-react';

interface Booking {
  id: string;
  room_name: string;
  date: string;
  time_slot: string;
  status: 'pending' | 'approved' | 'rejected' | 'completed' | 'cancelled';
  purpose?: string;
}

const Bookings = () => {
  const { toast } = useToast();
  const [email, setEmail] = useState('');
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  const handleViewBookings = async () => {
    if (!email) {
      toast({
        title: "Error",
        description: "Please enter your email address",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const { data, error } = await supabase
        .from('bookings')
        .select(`
          id,
          date,
          time_slot,
          purpose,
          status,
          rooms:room_id(id, name)
        `)
        .eq('email', email)
        .order('date', { ascending: false });

      if (error) throw error;

      const mappedBookings = data.map((booking: any) => ({
        id: booking.id,
        room_name: booking.rooms?.name || 'Unknown Room',
        date: booking.date,
        time_slot: booking.time_slot,
        status: booking.status,
        purpose: booking.purpose
      }));

      setBookings(mappedBookings);
    } catch (error) {
      console.error('Error fetching bookings:', error);
      toast({
        title: "Error",
        description: "Failed to fetch bookings. Please try again.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Layout>
      <PageHeader 
        title="Book a Room" 
        subtitle="Select a room and time slot to make your booking"
      />
      <div className="container mx-auto px-4 py-12">
        <Tabs defaultValue="new-booking" className="w-full mb-8">
          <TabsList className="grid grid-cols-2 w-full max-w-md mx-auto mb-6">
            <TabsTrigger value="new-booking">New Booking</TabsTrigger>
            <TabsTrigger value="my-bookings">My Bookings</TabsTrigger>
          </TabsList>
          
          <TabsContent value="new-booking">
            <div className="max-w-4xl mx-auto">
              <h2 className="text-2xl font-bold mb-6 text-center">Select Date, Room and Time</h2>
              <BookingCalendar />
            </div>
          </TabsContent>
          
          <TabsContent value="my-bookings">
            <div className="max-w-4xl mx-auto">
              <h2 className="text-2xl font-bold mb-6 text-center">View Your Bookings</h2>
              <Card className="mb-8">
                <CardContent className="p-6">
                  <p className="text-gray-600 mb-6">
                    Enter your email address to view your bookings.
                  </p>
                  <div className="flex gap-4">
                    <Input
                      type="email"
                      placeholder="Enter your email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      className="flex-1"
                    />
                    <Button 
                      onClick={handleViewBookings}
                      disabled={isLoading}
                    >
                      {isLoading ? 'Loading...' : 'View Bookings'}
                    </Button>
                  </div>
                </CardContent>
              </Card>

              {bookings.length > 0 ? (
                <div className="space-y-4">
                  {bookings.map((booking) => (
                    <Card key={booking.id}>
                      <CardContent className="p-6">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="font-semibold text-lg">{booking.room_name}</h3>
                            <p className="text-gray-600">
                              {format(new Date(booking.date), 'PPP')} at {booking.time_slot}
                            </p>
                            {booking.purpose && (
                              <p className="text-gray-600 mt-2">{booking.purpose}</p>
                            )}
                          </div>
                          <div className="flex items-center">
                            {booking.status === 'pending' ? (
                              <Clock className="h-4 w-4 mr-1 text-yellow-500" />
                            ) : booking.status === 'approved' ? (
                              <CheckCircle2 className="h-4 w-4 mr-1 text-green-500" />
                            ) : booking.status === 'rejected' ? (
                              <AlertCircle className="h-4 w-4 mr-1 text-red-500" />
                            ) : (
                              <AlertCircle className="h-4 w-4 mr-1 text-gray-500" />
                            )}
                            <span className="capitalize">{booking.status}</span>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              ) : email && !isLoading ? (
                <div className="text-center p-8 border rounded-lg bg-gray-50">
                  <p className="text-gray-600">No bookings found for this email address.</p>
                </div>
              ) : null}
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </Layout>
  );
};

export default Bookings;
