import React from 'react';

export const LoadingSpinner: React.FC = () => (
  <div style={{ 
    display: 'flex', 
    justifyContent: 'center', 
    alignItems: 'center', 
    height: '100vh',
    flexDirection: 'column',
    fontFamily: 'Arial, sans-serif'
  }}>
    <div style={{
      width: '50px',
      height: '50px',
      border: '5px solid #f3f3f3',
      borderTop: '5px solid #003366',
      borderRadius: '50%',
      animation: 'spin 1s linear infinite',
      marginBottom: '20px'
    }}></div>
    <p style={{ color: '#666' }}>Loading application...</p>
  </div>
);

export const ErrorDisplay: React.FC<{ title: string; message: string; details?: string }> = ({ title, message, details }) => (
  <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh', flexDirection: 'column', fontFamily: 'Arial, sans-serif' }}>
    <h1 style={{ color: 'red', marginBottom: '20px' }}>{title}</h1>
    <p style={{ marginBottom: '10px' }}>{message}</p>
    {details && (
      <pre style={{ background: '#f5f5f5', padding: '15px', borderRadius: '5px', maxWidth: '80%', overflow: 'auto', whiteSpace: 'pre-wrap' }}>
        {details}
      </pre>
    )}
  </div>
); 