import React from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import Layout from '@/components/layout/Layout';
import PageHeader from '@/components/ui/PageHeader';
import { Card, CardContent } from '@/components/ui/card';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Calendar, Clock, ArrowLeft, Trash, Edit } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { toast } from 'sonner';
import { supabase } from '@/integrations/supabase/client';
import { blogPosts } from '@/data/blogPosts';
import { BlogPost } from '@/components/blog/BlogCard';

interface Author {
  id: string;
  name: string;
  avatar_initials: string;
}

interface BlogPostData {
  id: string;
  title: string;
  excerpt: string;
  content: string;
  image_url: string;
  created_at: string;
  updated_at: string;
  author_id: string;
  published: boolean;
  author: Author[];
  date: string;
  readTime: string;
}

interface StaticBlogPost {
  id: number;
  title: string;
  excerpt: string;
  content: string;
  imageUrl: string;
  date: string;
  readTime: string;
  author: {
    name: string;
    avatarInitials: string;
  };
}

type DisplayPost = BlogPostData | StaticBlogPost;

// Helper function to convert BlogPost to StaticBlogPost
const convertToStaticPost = (post: BlogPost): StaticBlogPost => ({
  id: typeof post.id === 'string' ? parseInt(post.id, 10) : post.id,
  title: post.title,
  excerpt: post.excerpt,
  content: '', // Default empty content for static posts
  imageUrl: post.imageUrl,
  date: post.date,
  readTime: post.readTime,
  author: post.author
});

// Helper function to safely get post properties
const getPostProperty = <T extends DisplayPost>(
  post: T,
  prop: keyof BlogPostData | keyof StaticBlogPost,
  defaultValue: string | JSX.Element
): string | JSX.Element => {
  if (prop in post) {
    const value = post[prop as keyof T];
    if (prop === 'content') {
      return typeof value === 'string' ? value : defaultValue;
    }
    return typeof value === 'string' ? value : defaultValue;
  }
  return defaultValue;
};

// Helper function to get image URL
const getImageUrl = (post: DisplayPost): string => {
  if ('image_url' in post) {
    return post.image_url;
  }
  return post.imageUrl;
};

const fetchBlogPost = async (id: string): Promise<BlogPostData> => {
  const { data, error } = await supabase
    .from('blog_posts')
    .select(`
      *,
      author:blog_authors(
        id,
        name,
        avatar_initials
      )
    `)
    .eq('id', id)
    .single();

  if (error) {
    console.error('Error fetching blog post:', error);
    throw error;
  }

  return {
    ...data,
    author: data.author ? [data.author] : [],
    date: new Date(data.created_at).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }),
    readTime: '5 min read'
  };
};

const BlogDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();

  const { data: post, isLoading, error } = useQuery<BlogPostData, Error>({
    queryKey: ['blogPost', id],
    queryFn: () => fetchBlogPost(id!),
    enabled: !!id,
    retry: 1,
    staleTime: 1000 * 60 * 5,
  });

  // Fallback to static data if there's an error or no id
  const fallbackPost = !id ? undefined : blogPosts.find(p => p.id === Number(id));
  const displayPost = post || (fallbackPost ? convertToStaticPost(fallbackPost) : undefined);

  const handleDelete = async () => {
    if (!id || !confirm('Are you sure you want to delete this blog post?')) return;
    
    try {
      const { error } = await supabase
        .from('blog_posts')
        .delete()
        .eq('id', id);

      if (error) throw error;

      toast.success('Blog post deleted successfully');
      navigate('/blog');
    } catch (err) {
      console.error('Error deleting blog post:', err);
      toast.error('Failed to delete blog post');
    }
  };

  if (isLoading) {
    return (
      <Layout>
        <PageHeader title="Loading..." />
        <div className="container mx-auto px-4 py-12">
          <div className="animate-pulse max-w-4xl mx-auto">
            <div className="h-8 bg-gray-200 rounded w-3/4 mb-4"></div>
            <div className="h-4 bg-gray-200 rounded w-1/2 mb-8"></div>
            <div className="h-64 bg-gray-200 rounded mb-8"></div>
            <div className="h-4 bg-gray-200 rounded mb-2"></div>
            <div className="h-4 bg-gray-200 rounded mb-2"></div>
            <div className="h-4 bg-gray-200 rounded mb-2"></div>
          </div>
        </div>
      </Layout>
    );
  }

  if (!displayPost) {
    return (
      <Layout>
        <PageHeader 
          title="Blog Post Not Found" 
          subtitle="The requested blog post could not be found"
        />
        <div className="container mx-auto px-4 py-12">
          <Card>
            <CardContent className="p-6 text-center">
              <h1 className="text-2xl font-bold mb-4">404 - Post Not Found</h1>
              <p className="text-gray-600 mb-6">
                The blog post you're looking for doesn't exist or has been removed.
              </p>
              <Link to="/blog">
                <Button variant="outline">
                  <ArrowLeft className="mr-2 h-4 w-4" />
                  Back to Blog
                </Button>
              </Link>
            </CardContent>
          </Card>
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <PageHeader 
        title={displayPost.title}
        subtitle={`Posted on ${getPostProperty(displayPost, 'date', '')} • ${getPostProperty(displayPost, 'readTime', '5 min read')}`}
      />
      <div className="container mx-auto px-4 py-12">
        <div className="max-w-4xl mx-auto">
          <div className="mb-8">
            <div className="flex justify-between items-center mb-6">
              <Link to="/blog">
                <Button variant="outline">
                  <ArrowLeft className="mr-2 h-4 w-4" />
                  Back to Blog
                </Button>
              </Link>

              {post && (
                <div className="flex gap-2">
                  <Link to={`/blog/edit/${post.id}`}>
                    <Button variant="outline">
                      <Edit className="mr-2 h-4 w-4" />
                      Edit
                    </Button>
                  </Link>
                  <Button variant="destructive" onClick={handleDelete}>
                    <Trash className="mr-2 h-4 w-4" />
                    Delete
                  </Button>
                </div>
              )}
            </div>

            <div className="relative h-[400px] rounded-lg overflow-hidden mb-8">
              <img
                src={getImageUrl(displayPost)}
                alt={displayPost.title}
                className="w-full h-full object-cover"
              />
            </div>

            <div className="flex items-center justify-between mb-8">
              <div className="flex items-center">
                <Avatar className="h-10 w-10 mr-3">
                  <AvatarFallback className="bg-university-blue text-white">
                    {post?.author?.[0]?.avatar_initials || (displayPost as StaticBlogPost).author.avatarInitials}
                  </AvatarFallback>
                </Avatar>
                <div>
                  <p className="font-semibold">
                    {post?.author?.[0]?.name || (displayPost as StaticBlogPost).author.name}
                  </p>
                  <div className="flex items-center text-sm text-gray-500">
                    <Calendar className="h-4 w-4 mr-1" />
                    <span>{getPostProperty(displayPost, 'date', '')}</span>
                    <span className="mx-2">•</span>
                    <Clock className="h-4 w-4 mr-1" />
                    <span>{getPostProperty(displayPost, 'readTime', '5 min read')}</span>
                  </div>
                </div>
              </div>
            </div>

            <Card>
              <CardContent className="p-6">
                <div className="prose max-w-none">
                  <p className="text-lg text-gray-700 leading-relaxed">
                    {displayPost.excerpt}
                  </p>
                  <div className="text-lg text-gray-700 leading-relaxed mt-4">
                    {getPostProperty(displayPost, 'content', (
                      <>
                        <p className="mb-4">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
                        <p>Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
                      </>
                    ))}
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default BlogDetail;