import React, { createContext, useContext, useState, useEffect } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { useToast } from '@/hooks/use-toast';

export type UserPermissions = {
  canBook: boolean;
  canManageBookings: boolean;
  canManageUsers: boolean;
  canManageResources: boolean;
  canViewBookings: boolean;
  canVerifyBookings: boolean;
};

export type User = {
  id: string;
  email: string;
  role: string;
  permissions: UserPermissions;
};

type AuthContextType = {
  user: User | null;
  loading: boolean;
  signIn: (email: string, password: string) => Promise<{ session: any }>;
  signOut: () => Promise<void>;
  signUp: (email: string, password: string) => Promise<void>;
};

const defaultPermissions: UserPermissions = {
  canBook: true,
  canManageBookings: false,
  canManageUsers: false,
  canManageResources: false,
  canViewBookings: true,
  canVerifyBookings: false,
};

export const AuthContext = createContext<AuthContextType>({
  user: null,
  loading: true,
  signIn: async () => ({ session: null }),
  signOut: async () => {},
  signUp: async () => {},
});

export const useAuth = () => useContext(AuthContext);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  // Initialize auth state
  useEffect(() => {
    const initializeAuth = async () => {
      try {
        const { data: { session }, error } = await supabase.auth.getSession();
        if (error) throw error;

        if (session?.user) {
          await handleUserSession(session.user);
        }
      } catch (error) {
        console.error('Auth initialization error:', error);
        clearAuthState();
      } finally {
        setLoading(false);
      }
    };

    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (event, session) => {
      if (event === 'SIGNED_OUT') {
        clearAuthState();
      } else if (session?.user) {
        await handleUserSession(session.user);
      }
    });

    initializeAuth();

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  const handleUserSession = async (supabaseUser: any) => {
    try {
      const { data: profile, error: profileError } = await supabase
        .from('user_profiles')
        .select('role, permissions')
        .eq('id', supabaseUser.id)
        .single();

      if (profileError) throw profileError;

      const userData = {
        id: supabaseUser.id,
        email: supabaseUser.email!,
        role: profile?.role || 'user',
        permissions: profile?.permissions || defaultPermissions,
      };

      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));

      await updateLastActivity(supabaseUser.id);
    } catch (error) {
      console.error('User session handling error:', error);
      clearAuthState();
    }
  };

  const updateLastActivity = async (userId: string) => {
    await supabase
      .from('user_profiles')
      .update({ last_activity_at: new Date().toISOString() })
      .eq('id', userId);
  };

  const clearAuthState = () => {
    setUser(null);
    localStorage.removeItem('user');
  };

  const signIn = async (email: string, password: string) => {
    setLoading(true);
    try {
      const { data, error } = await supabase.auth.signInWithPassword({ email, password });
      if (error) throw error;
      if (!data.user) throw new Error('No user data received');

      // Get user profile
      const { data: profile, error: profileError } = await supabase
        .from('user_profiles')
        .select('role, permissions')
        .eq('id', data.user.id)
        .single();

      if (profileError) throw profileError;

      const userData = {
        id: data.user.id,
        email: data.user.email!,
        role: profile?.role || 'user',
        permissions: profile?.permissions || defaultPermissions,
      };

      setUser(userData);
      localStorage.setItem('user', JSON.stringify(userData));

      // Update last activity
      await updateLastActivity(data.user.id);

      return { session: data.session };
    } catch (error) {
      console.error('Sign in error:', error);
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const signOut = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
      clearAuthState();
      window.location.href = '/login';
    } catch (error) {
      console.error('Sign out error:', error);
      throw error;
    }
  };

  const signUp = async (email: string, password: string) => {
    try {
      const { data: authData, error: authError } = await supabase.auth.signUp({ email, password });
      if (authError) throw authError;
      if (!authData.user) throw new Error('No user data received');

      const { error: profileError } = await supabase
        .from('user_profiles')
        .insert([{
          id: authData.user.id,
          email,
          role: 'user',
          permissions: defaultPermissions,
          created_at: new Date().toISOString(),
          last_activity_at: new Date().toISOString()
        }]);

      if (profileError) throw profileError;

      toast.success("Account created", {
        description: "Please check your email to verify your account."
      });
    } catch (error) {
      console.error('Sign up error:', error);
      throw error;
    }
  };

  return (
    <AuthContext.Provider value={{ user, loading, signIn, signOut, signUp }}>
      {children}
    </AuthContext.Provider>
  );
}; 