import React from 'react';
import InnovationHubApplication from '@/components/forms/InnovationHubApplication';
import { InnovationHubForm } from '@/components/InnovationHubForm';

const InnovationHubApplicationPage = () => {
  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold mb-4">Innovation Hub Application</h1>
          <p className="text-gray-600 mb-6">Choose your preferred application method below</p>
          <div className="flex justify-center gap-4">
            <InnovationHubForm />
          </div>
        </div>
        <div className="mt-8">
          <InnovationHubApplication />
        </div>
      </div>
    </div>
  );
};

export default InnovationHubApplicationPage; 