import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { toast } from 'sonner';
import { Loader2 } from 'lucide-react';
import { supabase } from '@/integrations/supabase/client';

const InnovationHubApplication = () => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({
    studentNumber: '',
    fullName: '',
    email: '',
    phoneNumber: '',
    department: '',
    program: '',
    yearOfStudy: '',
    projectTitle: '',
    projectDescription: '',
    teamMembers: '',
    expectedOutcomes: '',
    resourcesNeeded: '',
  });

  useEffect(() => {
    const checkSession = async () => {
      const { data, error } = await supabase.auth.getSession();
      if (error || !data.session) {
        navigate('/login');
      }
    };
    checkSession();
  }, [navigate]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      // Store application in Supabase
      const { error } = await supabase
        .from('innovation_hub_applications')
        .insert([{
          ...formData,
          status: 'pending',
          created_at: new Date().toISOString(),
        }])
        .select()
        .single();

      if (error) throw error;

      // Create auth user
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: formData.email,
        password: formData.studentNumber, // Using student number as initial password
        options: {
          data: {
            full_name: formData.fullName,
            student_number: formData.studentNumber,
            role: 'student'
          }
        }
      });

      if (authError) throw authError;

      // Create user profile
      if (authData.user) {
        const { error: profileError } = await supabase
          .from('user_profiles')
          .insert([{
            id: authData.user.id,
            email: formData.email,
            full_name: formData.fullName,
            student_number: formData.studentNumber,
            role: 'student',
            permissions: {
              canBook: true,
              canManageBookings: false,
              canManageUsers: false,
              canManageResources: false
            }
          }]);

        if (profileError) throw profileError;
      }

      // Send email notification
      try {
        const { error: emailError } = await supabase.functions.invoke('send-application-email', {
          body: {
            to: 'mapseujers@gmail.com',
            subject: 'New Innovation Hub Application',
            applicationData: formData
          }
        });

        if (emailError) {
          console.warn('Email notification failed:', emailError);
          // Continue with the application process even if email fails
        }
      } catch (emailError) {
        console.warn('Email notification failed:', emailError);
        // Continue with the application process even if email fails
      }

      toast.success("Application Submitted", {
        description: "Your application has been submitted successfully. You can now log in using your email and student number as the password.",
      });

      navigate('/login');
    } catch (error: any) {
      console.error('Application error:', error);
      toast.error("Application Failed", {
        description: error.message || "Failed to submit application. Please try again.",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6 max-w-2xl mx-auto p-6">
      <div className="space-y-4">
        <h2 className="text-2xl font-bold text-center">Innovation Hub Application Form</h2>
        <p className="text-center text-gray-600">Please fill in all the required information below</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="studentNumber">Student Number *</Label>
          <Input
            id="studentNumber"
            name="studentNumber"
            value={formData.studentNumber}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="fullName">Full Name *</Label>
          <Input
            id="fullName"
            name="fullName"
            value={formData.fullName}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="email">Email Address *</Label>
          <Input
            id="email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="phoneNumber">Phone Number *</Label>
          <Input
            id="phoneNumber"
            name="phoneNumber"
            value={formData.phoneNumber}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="department">Department *</Label>
          <Input
            id="department"
            name="department"
            value={formData.department}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="program">Program *</Label>
          <Input
            id="program"
            name="program"
            value={formData.program}
            onChange={handleInputChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="yearOfStudy">Year of Study *</Label>
          <Input
            id="yearOfStudy"
            name="yearOfStudy"
            value={formData.yearOfStudy}
            onChange={handleInputChange}
            required
          />
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="projectTitle">Project Title *</Label>
        <Input
          id="projectTitle"
          name="projectTitle"
          value={formData.projectTitle}
          onChange={handleInputChange}
          required
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="projectDescription">Project Description *</Label>
        <Textarea
          id="projectDescription"
          name="projectDescription"
          value={formData.projectDescription}
          onChange={handleInputChange}
          required
          rows={4}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="teamMembers">Team Members (if any)</Label>
        <Textarea
          id="teamMembers"
          name="teamMembers"
          value={formData.teamMembers}
          onChange={handleInputChange}
          rows={2}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="expectedOutcomes">Expected Outcomes *</Label>
        <Textarea
          id="expectedOutcomes"
          name="expectedOutcomes"
          value={formData.expectedOutcomes}
          onChange={handleInputChange}
          required
          rows={3}
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="resourcesNeeded">Resources Needed *</Label>
        <Textarea
          id="resourcesNeeded"
          name="resourcesNeeded"
          value={formData.resourcesNeeded}
          onChange={handleInputChange}
          required
          rows={3}
        />
      </div>

      <Button
        type="submit"
        className="w-full bg-university-blue hover:bg-university-blue/90"
        disabled={isLoading}
      >
        {isLoading ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            Submitting...
          </>
        ) : 'Submit Application'}
      </Button>
    </form>
  );
};

export default InnovationHubApplication; 