// This file is automatically generated. Do not edit it directly.

import { createClient } from '@supabase/supabase-js';

// Get environment variables
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

// Validate environment variables
if (!supabaseUrl || !supabaseAnonKey) {
  throw new Error('Missing Supabase environment variables. Please check your .env file.');
}

// Validate URL format
try {
  new URL(supabaseUrl);
} catch (error) {
  throw new Error('Invalid Supabase URL format. Please check your VITE_SUPABASE_URL.');
}

// Create a single instance of the Supabase client to avoid multiple instances
export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    persistSession: true,  // Enable session persistence
    autoRefreshToken: true, // Enable token auto-refresh
    detectSessionInUrl: true, // Detect session in URL for OAuth callbacks
    flowType: 'pkce',
    storageKey: 'nust-dev-reserve-auth',
    storage: {
      getItem: (key) => {
        try {
          return Promise.resolve(localStorage.getItem(key));
        } catch (error) {
          console.error('Error reading from localStorage:', error);
          return Promise.resolve(null);
        }
      },
      setItem: (key, value) => {
        try {
          localStorage.setItem(key, value);
          return Promise.resolve();
        } catch (error) {
          console.error('Error writing to localStorage:', error);
          return Promise.resolve();
        }
      },
      removeItem: (key) => {
        try {
          localStorage.removeItem(key);
          return Promise.resolve();
        } catch (error) {
          console.error('Error removing from localStorage:', error);
          return Promise.resolve();
        }
      }
    }
  },
  global: {
    headers: {
      'Content-Type': 'application/json',
    },
  },
});

// Export a function to access the client as well
export const getSupabase = () => supabase;

// Test the connection
export const testConnection = async () => {
  try {
    const { data, error } = await supabase.auth.getSession();
    if (error) {
      console.error('Supabase auth error:', error.message);
      throw error;
    }
    console.log('Supabase connection successful');
    return true;
  } catch (error) {
    console.error('Supabase connection failed:', error);
    return false;
  }
};

// Helper function to check if Supabase is properly configured
export const checkSupabaseConfig = () => {
  const issues = [];
  
  if (!supabaseUrl) {
    issues.push('Missing VITE_SUPABASE_URL');
  }
  
  if (!supabaseAnonKey) {
    issues.push('Missing VITE_SUPABASE_ANON_KEY');
  }
  
  if (issues.length > 0) {
    console.error('Supabase configuration issues:', issues.join(', '));
    return false;
  }
  
  return true;
};

