import React from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { useQuery, useMutation, useQueryClient, QueryClient, UseQueryOptions } from '@tanstack/react-query';
import { z } from 'zod';
import { zodResolver } from '@hookform/resolvers/zod';
import Layout from '@/components/layout/Layout';
import PageHeader from '@/components/ui/PageHeader';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { toast } from 'sonner';
import { supabase } from '@/integrations/supabase/client';

// Define types
interface AuthorProfile {
  id: string;
  name: string;
  avatar_initials: string;
}

interface BlogPost {
  id: string;
  title: string;
  excerpt: string;
  content: string;
  image_url: string;
  author_id: string;
  published?: boolean;
  created_at?: string;
  updated_at?: string;
}

// Zod schema
const formSchema = z.object({
  title: z.string().min(2, { message: 'Title must be at least 2 characters.' }),
  excerpt: z.string().min(10, { message: 'Excerpt must be at least 10 characters.' }),
  content: z.string().min(20, { message: 'Content must be at least 20 characters.' }),
  image_url: z.string().url({ message: 'Please enter a valid URL.' }),
});

type FormValues = z.infer<typeof formSchema>;

// Data fetching functions
const fetchCurrentUserProfile = async (): Promise<AuthorProfile> => {
  const { data: { user }, error: authError } = await supabase.auth.getUser();
  
  if (authError || !user) {
    throw new Error('Not authenticated');
  }

  const { data, error } = await supabase
    .from('profiles')
    .select('id, name, avatar_initials')
    .eq('id', user.id)
    .single();

  if (error) throw error;
  return data as AuthorProfile;
};

const fetchPost = async (id: string): Promise<BlogPost> => {
  const { data, error } = await supabase
    .from('posts')
    .select('*')
    .eq('id', id)
    .single();

  if (error) throw error;
  return data as BlogPost;
};

const BlogForm = () => {
  const [isMounted, setIsMounted] = React.useState(false);
  const navigate = useNavigate();
  const { id } = useParams();
  const isEditing = !!id;
  const queryClient = useQueryClient();
  
  // Properly typed queries
  const { data: author } = useQuery<AuthorProfile, Error>({
    queryKey: ['currentUserProfile'],
    queryFn: fetchCurrentUserProfile,
    retry: 1,
    gcTime: 0,
    staleTime: 0,
    onError: (error: Error) => {
      console.error('Error fetching user profile:', error);
      toast.error('You need to be logged in to create or edit blog posts');
      navigate('/login');
    }
  } as UseQueryOptions<AuthorProfile, Error>);

  const { data: post } = useQuery<BlogPost, Error>({
    queryKey: ['blogPost', id],
    queryFn: () => fetchPost(id!),
    enabled: isEditing,
    retry: 1,
    gcTime: 0,
    staleTime: 0,
    onError: (error: Error) => {
      console.error('Error fetching post:', error);
      toast.error('Failed to fetch blog post');
      navigate('/blog');
    }
  } as UseQueryOptions<BlogPost, Error>);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      title: '',
      excerpt: '',
      content: '',
      image_url: '',
    },
    mode: 'onChange',
  });

  React.useEffect(() => {
    setIsMounted(true);
    if (post) form.reset(post);
  }, [post, form]);

  const mutation = useMutation<BlogPost, Error, FormValues>({
    mutationFn: async (values) => {
      if (!author) throw new Error('No author profile found');
      
      const postData = {
        title: values.title,
        excerpt: values.excerpt,
        content: values.content,
        image_url: values.image_url,
        updated_at: new Date().toISOString()
      };

      if (isEditing) {
        const { data, error } = await supabase
          .from('blog_posts')
          .update(postData)
          .eq('id', id!)
          .select()
          .single();
        
        if (error) throw error;
        return data as BlogPost;
      } else {
        const { data, error } = await supabase
          .from('blog_posts')
          .insert({
            ...postData,
            author_id: author.id,
            published: true,
            created_at: new Date().toISOString()
          })
          .select()
          .single();
        
        if (error) throw error;
        return data as BlogPost;
      }
    },
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: ['blogPosts'] });
      queryClient.invalidateQueries({ queryKey: ['blogPost', data.id] });
      toast.success(isEditing ? 'Blog post updated!' : 'Blog post created!');
      navigate(`/blog/${data.id}`);
    },
    onError: (error: Error) => {
      console.error('Error saving blog post:', error);
      toast.error('Failed to save blog post');
    }
  });

  const handleSubmit = (values: FormValues) => {
    mutation.mutate(values);
  };

  if (!isMounted) return null;

  return (
    <Layout>
      <PageHeader title={isEditing ? 'Edit Blog Post' : 'Create Blog Post'} />
      <div className="container mx-auto px-4 py-12">
        <Card className="max-w-3xl mx-auto">
          <CardContent className="p-6">
            <Form {...form}>
              <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-4">
                {/* Form fields remain the same as before */}
                <FormField
                  control={form.control}
                  name="title"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Title</FormLabel>
                      <FormControl>
                        <Input placeholder="My Awesome Blog Post" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="excerpt"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Excerpt</FormLabel>
                      <FormControl>
                        <Textarea
                          placeholder="A brief summary of the blog post"
                          className="resize-none"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="content"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Content</FormLabel>
                      <FormControl>
                        <Textarea
                          placeholder="The main content of the blog post"
                          className="resize-none h-80"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="image_url"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Image URL</FormLabel>
                      <FormControl>
                        <Input placeholder="URL of the blog post image" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <div className="flex justify-end">
                  <Button type="submit" disabled={mutation.isPending}>
                    {mutation.isPending ? 'Saving...' : 'Save Post'}
                  </Button>
                </div>
              </form>
            </Form>
          </CardContent>
        </Card>
      </div>
    </Layout>
  );
};

export default BlogForm;